
.content {
    padding-top: 40px;
}

.headerText{
    text-align: center;
    margin-top: 10px;
}

.buttons {
    text-align: center;
}

.buttons button{
    margin: 40px;
}

* {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  color: #333;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.6; }

.header {
  background: #20232a; }

.header__title {
  color: white;
  text-decoration: none; }
  .header__title h1 {
    margin: 0; }

.header--link {
  color: #f7f7f7;
  padding-right: 4.8rem; }

.login--form {
  width: 50%;
  margin: 0 auto;
  margin-top: 10%; }

.login--inner {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 1.2rem; }

.input--form {
  font-size: 100%;
  margin-bottom: 15px; }

.login--title {
  text-align: center;
  letter-spacing: 1px;
  margin-bottom: 10px; }

.form--alert {
  text-align: center;
  width: 50%;
  margin: 0 auto;
  letter-spacing: 1px; }

.user-button {
  margin-top: 20px; }

html, body {
  width: 100%;
  height: 100%;
  position: absolute; }

.side_section {
  background: #0F2027;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #2C5364, #203A43, #0F2027);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

.side_section {
  color: #F5F5F5;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3); }

.submit_button {
  text-align: center; }

.side_section_date {
  font-size: 1.3em;
  text-shadow: 0 4px 3px rgba(0, 0, 0, 0.4), 0 8px 13px rgba(0, 0, 0, 0.1), 0 18px 23px rgba(0, 0, 0, 0.1);
  letter-spacing: .075em;
  padding-top: 10px;
  padding-bottom: 30px; }

.side_section_user_data {
  padding-top: 30px; }

.side_section_user_data h2 {
  padding-bottom: 10px; }

.userData {
  color: orange; }

.side_section_stats {
  padding-top: 90px;
  padding-bottom: 18vh; }

.side_section_stats h2 {
  font-size: 1.2em;
  padding-bottom: 10px; }

.section_header {
  font-size: 2em;
  padding-bottom: 20px;
  padding-top: 40px; }

.award_table thead {
  font-size: .85em;
  font-weight: bold; }

.award_data th {
  font-weight: normal; }

.input_width {
  width: 100%; }

.award_creation_buttons {
  text-align: center; }

.updateAccountButton {
  background-color: #BF7517;
  border: none;
  border-radius: 5px;
  color: white; }

.updateAccountButton:hover {
  background-color: #B56F16; }

.sideSectionRow {
  font-size: 1em;
  padding-left: 10px; }

.removeColumnPadding {
  padding: 0; }

.form_padding Input {
  border-radius: 2px;
  box-shadow: 1px 1px 4px black;
  padding: 1px 5px; }

.button_style {
  padding: 5px 10px;
  width: 75px;
  height: 35px;
  font: bold 15px Helvetica, sans-serif;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3); }

.text_style {
  text-shadow: 1px 2px rgba(0, 0, 0, 0.1); }

.delete_award_button {
  background-color: #C90700;
  width: 70px;
  height: 30px;
  font: 15px Helvetica, sans-serif;
  letter-spacing: .075em;
  text-shadow: 1px 2px rgba(0, 0, 0, 0.1); }

.delete_award_button:hover {
  background-color: #B00600; }

.cancel_create_award {
  background-color: #C90700;
  border: none; }

.cancel_create_award:hover {
  background-color: #B00600; }

.confirm_button_spacing Button {
  margin: 2px; }

.confirm_buttons {
  text-shadow: 1px 2px rgba(0, 0, 0, 0.1);
  font: 11px Helvetica, sans-serif; }

.delete_button_color {
  background-color: #C90700; }

.delete_button_color:hover {
  background-color: #B00600; }

.admin--dash {
  padding-top: 3.2rem; }

.admin--menu {
  margin: 0;
  margin-top: 50px;
  width: 200px;
  height: 100%;
  overflow: auto; }

.admin--menu a {
  display: block;
  color: black;
  padding: 1.2rem;
  text-decoration: none;
  text-align: left;
  border: 1px solid black; }

.admin--menu a.active {
  background: #0F2027;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #2C5364, #203A43, #0F2027);
  color: white; }

.admin--menu a:hover:not(.active) {
  background: #0F2027;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #2C5364, #203A43, #0F2027);
  color: white; }

.admin--menu h1 {
  text-align: left;
  padding: 0 1.2rem;
  text-decoration: underline; }

.admin--dash--content {
  margin-left: 1%;
  padding: 15px 2px;
  height: 1000px; }

.admin--title h1 {
  float: left; }

.admin--add {
  float: right; }

.report {
  margin: 3.2rem; }

.home {
  align-items: center;
  background: url("/images/homeImage.jpg");
  background-size: cover;
  display: flex;
  height: 95vh;
  justify-content: center; }

.home--title {
  color: white;
  font-size: 9rem;
  border-radius: 3px;
  text-shadow: 0 0 20px black; }

.home--button {
  font-size: 2rem;
  width: 15rem;
  border: none; }

.home--login {
  text-align: center; }

.blurred--background {
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.5);
  /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 2;
  width: 75%;
  padding: 60px 0;
  text-align: center; }

.error--page {
  text-align: center;
  margin-top: 4.8rem; }

.error-title {
  padding-top: 1.6rem;
  font-size: 4.8rem; }

